import React, { useState } from 'react'
// import { useState } from 'react'
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { http } from '../../Util/setting';
// import {useState} from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getUserList } from '../../Redux/action/UserAction';
import { useEffect } from 'react';
import swal from 'sweetalert';

export default function UserUpdateModal(props) {
    //Handle addUser
    const dispatch = useDispatch()
    const updateUser = async (values) => {
        try {
            let result = await http.put('/api/QuanLyNguoiDung/CapNhatThongTinNguoiDung', values)
            if (result.request.status === 200) {
                formik.resetForm()
                // alert('Cp nht thnh cng')
                swal({
                    title: "Cp nht thnh cng",
                    icon: "success",
                    timer: 2000,
                    button: false,
                });
                dispatch(getUserList)

            }

        } catch (errors) {
            // alert(errors.response.data)
            swal({
                title: errors.response?.data,
                icon: "warning",
                text: ' xy ra li vui lng quay li trang ch hoc th li',
                timer: 2000,
                button: false,
            });
        }
    }
    
    const formik = useFormik({
        initialValues: {
            taiKhoan: '',
            matKhau: '',
            hoTen: '',
            soDt: '',
            maLoaiNguoiDung: '',
            maNhom: 'GP01',
            email: ''
        },
        validationSchema: Yup.object().shape({
            taiKhoan: Yup.string()
                .min(2, 'Ti khon qu t k t')
                .max(16, 'Ti khon qu 16 k t')
                .required('Ti khon khng c  trng'),

            matKhau: Yup.string()
                .required('Nhp li mt khu')
                .matches(/^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/, 'Mt khu phi t nht 8 k t gm ch, s, v k t c bit'),

            hoTen: Yup.string()
                .required('Tn khng c  trng')
                .matches(/^[a-zA-Z_" + "" + "\\s]+$/, 'Ch nhp k t ch'),

            email: Yup.string().email('Email khng hp l').required('Email khng c  trng'),

            soDt: Yup.string()
                .required('S in thoi khng c  trng')
                .matches(/([\+84|84|0]+(3|5|7|8|9|1[2|6|8|9]))+([0-9]{8})\b/, 'S in thoi cha ng nh ng'),
            maLoaiNguoiDung: Yup.string().required('Vui lng chn ngi dng !'),
        }),
        onSubmit: updateUser
    }

    )
    useEffect(() => {
        props.userUpdate.maNhom="GP01"
        // console.log(props.userUpdate)
        formik.setValues(props.userUpdate)
    }, [props.userUpdate])
    return (
        <div className="modal fade" id="userUpdate">
            <div className="modal-dialog formUser">
                <div className="modal-content px-3">
                    <header className="head-form mb-0">
                        <h3 id="header-title">THNG TIN NGI DNG</h3>
                    </header>

                    <div className="modal-body">
                        <form onSubmit={formik.handleSubmit} role="form">
                            <div className="form-group">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                        <span className="input-group-text"><i className="fa fa-user" /></span>
                                    </div>
                                    <input value={formik.values.taiKhoan} onChange={formik.handleChange} onBlur={formik.handleBlur} name="taiKhoan" id="tknv" className="form-control input-sm" placeholder="Ti khon" />
                                </div>
                                {formik.errors.taiKhoan && <div className="text-danger text-left">{formik.errors.taiKhoan}</div>}
                            </div>
                            <div className="form-group">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                        <span className="input-group-text"><i className="fa fa-address-book" /></span>
                                    </div>
                                    <input value={formik.values.hoTen} onChange={formik.handleChange} onBlur={formik.handleBlur} type="name" name="hoTen" id="name" className="form-control input-sm" placeholder="H v tn" />
                                </div>
                                {formik.errors.hoTen && <div className="text-danger text-left">{formik.errors.hoTen}</div>}
                            </div>
                            <div className="form-group">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                        <span className="input-group-text"><i className="fa fa-envelope" /></span>
                                    </div>
                                    <input value={formik.values.email} onChange={formik.handleChange} onBlur={formik.handleBlur} type="email" name="email" id="email" className="form-control input-sm" placeholder="Email" />
                                </div>
                                {formik.errors.email && <div className="text-danger text-left">{formik.errors.email}</div>}
                            </div>
                            <div className="form-group">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                        <span className="input-group-text"><i className="fa fa-key" /></span>
                                    </div>
                                    <input value={formik.values.matKhau} onChange={formik.handleChange} onBlur={formik.handleBlur} type="password" name="matKhau" id="password" className="form-control input-sm" placeholder="Mt khu" />
                                </div>
                                {formik.errors.matKhau && <div className="text-danger text-left">{formik.errors.matKhau}</div>}
                            </div>
                            <div className="form-group">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                        <span className="input-group-text"><i className="fa fa-phone" /></span>
                                    </div>
                                    <input value={formik.values.soDt} onChange={formik.handleChange} onBlur={formik.handleBlur} type="text" name="soDt" id="datepicker" className="form-control" placeholder="S in thoi" />
                                </div>
                                {formik.errors.soDt && <div className="text-danger text-left">{formik.errors.soDt}</div>}
                            </div>
                            <div className="form-group">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                        <span className="input-group-text"><i className="fa fa-briefcase" /></span>
                                    </div>
                                    <select value={formik.values.maLoaiNguoiDung} onChange={formik.handleChange} onBlur={formik.handleBlur} className="form-control" id="chucvu" name="maLoaiNguoiDung">
                                        <option value="">Loi ngi dng</option>
                                        <option value="GV">Gio v</option>
                                        <option value="HV">Hc vin</option>
                                    </select>
                                </div>
                                {formik.errors.maLoaiNguoiDung && <div className="text-danger text-left">{formik.errors.maLoaiNguoiDung}</div>}
                            </div>
                            
                            <div className="modal-footer" id="modal-footer">
                                <button id="btnThem" type="submit" className="btn">Cp nht</button>
                                <button id="btnDong" type="button" className="btn btn-danger" data-dismiss="modal">ng</button>
                            </div>

                        </form>
                    </div>
                    {/* Modal footer */}

                </div>
            </div>
        </div>
    )
}
